import React, { Component } from 'react';
import { Table, Divider, Spin } from 'antd';

const columns = [{
  title: 'ชื่อวิชา',
  dataIndex: 'subject',
  key: 'subject',
  render: text => <a href="javascript:;">{text}</a>,
}, {
  title: 'รายละเอียด',
  dataIndex: 'description',
  key: 'description',
}, {
  title: 'ประเภท',
  dataIndex: 'type',
  key: 'type',
}, {
  title: 'วันกำหนดส่ง',
  dataIndex: 'date',
  key: 'date',
}, {
  title: 'เวลากำหนดส่ง',
  dataIndex: 'time',
  key: 'time',
}, {
  title: 'แจ้งล่วงหน้า​ (วัน)',
  dataIndex: 'appointment',
  key: 'appointment',
}, {
  title: 'Action',
  key: 'action',
  render: (text, record) => (
    <span>
      <a href="javascript:;">แก้ไข</a>
      <Divider type="vertical" />
      <a href="javascript:;">ลบ</a>
    </span>
  ),
}];

class TableItems extends Component {
  render() {
    const  { data, isLoading } = this.props;

    if (isLoading)
      return (<Spin />)

    return (
      <Table columns={columns} dataSource={this.convertedData(data)} />
    );
  }
}

export default TableItems;


